import { Link } from "gatsby";
import React, { FC } from "react";
import styled from "styled-components";
import { NavigationItem } from "../../../../../types/NavigationItem";
import { colors } from "../../../../../utils/colors";

interface Props {
  navigationItem: NavigationItem;
}

export const StyledDesktopNavSection = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: fit-content;

  a:hover {
    color: ${colors.primary.lightOrange};
  }

  & > div:first-child {
    font-size: 0.7rem;
    font-weight: 700;
  }
  & > div:nth-child(2) {
    font-size: 0.6rem;
    display: grid;
    gap: 5px 20px;
    max-height: 80px;
    flex-shrink: 0;
  }
`;

const FooterLinkWrapper = styled.div`
  text-wrap: nowrap;
`

const DesktopNavSection: FC<Props> = ({ navigationItem }) => {
  return (
    <StyledDesktopNavSection>
      <div>{navigationItem.sectionTitle}</div>
      <div>
        {navigationItem.links.map((link) => {
          return (
            <FooterLinkWrapper key={link.linkId}>
              {link.external ? (
                <a href={link.url} target="_blank" key={link.linkId} rel="noreferrer">
                  {link.title}
                </a>
              ) : (
                <Link to={link.url} key={link.linkId}>
                  {link.title}
                </Link>
              )}
            </FooterLinkWrapper>
          );
        })}
      </div>
    </StyledDesktopNavSection>
  );
};

export default DesktopNavSection;
